import React from "react";
import { DownloadOutlined } from '@ant-design/icons';
import { CSVLink } from "react-csv";
 
class Download extends React.PureComponent{
     constructor(props){
         super(props);
         this.state={
             csvData: props.data
         }
     }
    
     componentWillReceiveProps(nextProps){
         this.setState({
             csvData: nextProps.data
         })
     }

    render(){
        return(<CSVLink data={this.state.csvData} filename={this.props.fileName} target="_blank" style={{cursor:"pointer !important", color:this.props.color}}><DownloadOutlined /><b>Download</b></CSVLink>)
    }
}
export default Download;