  constructor(props) {
    super(props);
    this.state = {
      userName: "",
      historyData: "",
      historyDownload: "",
      historyLoad: false,
    };
  }
  componentDidMount() {
    this.setState({
      historyLoad: true,
    });
    // axios
      // .get(
      //   process.env.REACT_APP_DOMAIN +
      //     "/getUserDetails?authUsername=prajwal&email=kovoorprajwal%40gmail.com"
      // ) //for  local
      axios.get(process.env.REACT_APP_DOMAIN + '/getUserDetails')//for server
      .then((response) => {
        if (response.data.operationCode == 200) {
          this.setState(
            {
              userName: response.data.data[0].username,
            },
            () => {
              this.historyData();
            }
          );
        } else {
          this.setState({
            loading: false,
          });
        }
      })
      .catch((err) => {
        this.setState({
          loading: false,
        });
      });
  }

