import React from 'react';
import { Row, Col, Tabs } from 'antd';
import 'antd/dist/antd.css';
import "./Home.css";
import Colombia from '../colombia1/Colombia/Colombia';
import Argentina from '../argentina/Argentina/Argentina';
import Peru from '../peru/Peru/Peru';
import Hp from '../../assets/Hp.png';
import axios from 'axios';

const { TabPane } = Tabs;

class Tab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            userName: '',
        }
    }

    componentDidMount() {
        // axios.get(process.env.REACT_APP_DOMAIN + '/getUserDetails?authUsername=prajwal&email=kovoorprajwal%40gmail.com')//for  local
            axios.get(process.env.REACT_APP_DOMAIN + '/getUserDetails')//for server
            .then(response => {
                if (response.data.operationCode == 200) {
                    this.setState({
                        userName: response.data.data[0].username
                    })
                }
            })
            .catch(err => err)
    }

    render() {
        return (
            <div className="card-container">
                <Row style={{ marginBottom: '1.2em' }}>
                    <Col span={10} style={{ marginTop: "1%", marginLeft: '0em' }}><img src={Hp} style={{ width: "3.4em" }} alt="Hp logo" /><span style={{ color: "#0095d9", fontWeight: 600, fontSize: "15.5px", marginLeft: "0.3em" }}> LATAM QUALIFIED DISTRIBUTION NETWORK</span></Col>
                    <Col span={9}></Col>
                    <Col span={4} style={{ marginTop: "1.5%", fontSize: "15px", color: "#0095d9", fontWeight: 600 }}><label style={{ cursor: "pointer" }}>Welcome: </label><strong>{this.state.userName}</strong></Col>
                </Row>

                <Tabs type="card" className="tab-parent" >

                    <TabPane tab="COLOMBIA" key="1">
                        <Colombia />
                    </TabPane>
                    <TabPane tab="ARGENTINA" key="2">
                        <Argentina />
                    </TabPane>
                    <TabPane tab="PERU" key="3">
                        <Peru />
                    </TabPane>

                </Tabs>
            </div>
        )
    }
}
export default Tab;