  uploadFileForArgentina = (event) => {
    console.log('coming in argentina')
    notification.open({
      message: "Upload Data",
      description: "We have received your request, will be processed soon!",
      duration: 4,
      icon: <SmileOutlined style={{ color: "#108ee9" }} />,
    });
    this.setState(
      {
        uploadFile: event.target.files[0],
      },
      () => {
        this.uploadFile1();
      }
    );
    event.target.value = "";
  };

  uploadFile1 = () => {
    console.log('coming in argentina1')
    const formData = new FormData();
    formData.append("file", this.state.uploadFile);
    formData.append("submittedOn", moment().format("YYYY/MM/DD"));
    formData.append("submittedBy", this.state.userName);
    formData.append("comment", "Bulk Data Request");
    const config = {
      headers: { "content-type": "multipart/form-data" },
    };

    axios
      .post(
        process.env.REACT_APP_DOMAIN + "/uploadRecordsForArgentina",
        formData,
        config
      )
      .then((response) => {
        if (response.data == true) {
          message.success("Successfully Uploaded!", 3);
          this.setState(
            {
              uploadedSuccesfully: true,
            },
            () => {
              this.componentDidMount();
            }
          );
        } else {
          message.error(response.data, 3);
          this.setState({
            uploadedSuccesfully: false,
          });
        }
      })
      .catch((err) => {
        message.error("Something went wrong!");
        this.setState({
          uploadedSuccesfully: false,
        });
      });
  };

