
import React from 'react';
import 'antd/dist/antd.css';
import './Dropdown.css';
import { Select } from 'antd';

const { Option } = Select;

class Dropdown extends React.Component {
    constructor(props) {
        super(props);
        if (props.value !== "") {
            this.state = {
                val: props.value
            }
        }
        else {
            this.state = {
                val: []
            }
        }
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.value !== "")
            this.setState({ val: nextProps.value });
        else
            this.setState({ val: nextProps.defaultValue });
    }

    handleChange = (value) => {
        //alert(`selected ${value}`);
        this.props.select(value, this.props.id);
    }
    render() {
        return (
            <Select mode={this.props.mode} allowClear defaultValue={this.props.defaultValue} style={{ width: this.props.width, borderRadius: '5px', color: '#8e8e8e', fontWeight: 600 }} showSearch onChange={this.handleChange} placeholder={this.props.placeholder} value={this.state.val} >
                {
                    this.props.data.map((data, i) => {
                        return (
                            <Option value={data} style={{ color: '#8e8e8e', fontWeight: 600 }} key={i}>{data}</Option>
                        )
                    })
                }
            </Select>
        )
    }
}

export default Dropdown;